import React from "react"
import {connect} from "react-redux"
import {withRouter} from 'react-router-dom';
import ArticleList from "../views/ArticleList"
import {getMarkdownList} from "../../api/markdownApi"
import {changeSearch} from "../../actions/markdownActions"
import Spinner from "../widgets/Spinner"
import PropTypes, {array, arrayOf, func, string} from 'prop-types';

import {SEARCH_CHANGE} from "../../actions/actionTypes"
import store from "../../store"

/**
 * 文章列表
 */
class ArticleListContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {quote: {}};
    this.fetchQuote = this.fetchQuote.bind(this);
  }

  componentDidMount() {
    this.props.onSearch();
    document.title = 'Promise.rocks';
    this.fetchQuote();
  }

  fetchQuote() {
    fetch('https://promise.rocks/logically/quote')
      .then(r => r.json())
      .then(r => {
        r.ok && this.setState({quote: r.quote});
      })
      .catch(console.warn);
  }

  render() {
    const {articles} = this.props;
    if (!articles) {
      return <Spinner/>
    }
    return <ArticleList
      quote={this.state.quote}
      fetchQ={this.fetchQuote}
      articles={this.props.articles || []}
      onSearch={this.props.onSearch}
      changeSearch={this.props.changeSearch}
      search={this.props.search}
    />
  }
}

ArticleListContainer.propTypes = {
  articles: arrayOf(PropTypes.shape({
    id: string.isRequired,
    title: string.isRequired,
    content: string,
    modified: PropTypes.oneOfType([string, PropTypes.instanceOf(Date)]).isRequired
  })),
  onSearch: func.isRequired,
  changeSearch: func.isRequired,
  search: string
};


const mapStateToProps = store => {
  return {
    articles: store.markdownState.markdownList,
    limit: store.markdownState.limit,
    search: store.search
  }
};

const mapDispatchToProps = {
  onSearch: getMarkdownList,
  changeSearch
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(ArticleListContainer));
