import React from "react"
import {Link} from "react-router-dom"
import md_render from "../../lib/md"

/**
 * markdown 列表
 */
export default (props) => {
  const {onSearch, changeSearch, search} = props;
  return <main className="container">

    <Search {...{onSearch, changeSearch, search}}/>
    <section data-id={props.quote._id} className="message">
      <span className="fa fa-book" onClick={props.fetchQ}/>
      {props.quote.quote}
    </section>
    <ul className="articles">
      {
        props.articles.map(article)
      }
    </ul>
  </main>
}

/**
 * article item
 */
const article = (md, i) => (<li key={md.id}>
  <Link to={'/article/' + md.id}>
    <i className="fa fa-pencil"/>
    <span dangerouslySetInnerHTML={{__html: md_render.renderInline(md.title || md.id)}}/>
  </Link>
</li>);

const Search = ({onSearch, changeSearch, search}) => {
  return <p className="control">
    <input className="input" type="search" placeholder="Search..." value={search} onChange={e => {
      changeSearch(e.target.value)
    }} onKeyDown={(e) => {
      if (e.key === 'Enter') {
        onSearch();
      }
    }}/>
  </p>
};
